@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{padding:2rem;display:flex;justify-content:space-between;align-items:center;box-shadow:0px 0px 4px 0px rgba(0,0,0,0.4)}nav a{text-decoration:none}nav .toggle-icon{font-size:2.4rem}

.country-wrapper{border-radius:6px;overflow:hidden;max-width:35rem;margin:1rem;box-shadow:0px 0px 5px 0px rgba(0,0,0,0.3);max-width:30rem;cursor:pointer}.country-wrapper .country-flag img{object-position:center;object-fit:cover;width:30rem;height:20rem}.country-wrapper .country-info{padding:2rem}.country-wrapper .country-info li{margin:0.5rem 0}.country-wrapper .country-info span{font-weight:800}

.filter-input{box-shadow:0px 0px 4px 1px rgba(0,0,0,0.4);width:14.6rem}.filter-input .disabled{display:none}.filter-input select{padding:1rem;padding-right:2rem;border:none}

.search-input{position:relative}.search-input input{box-shadow:0px 0px 4px 0px rgba(0,0,0,0.4);width:100%;padding:1rem;padding-left:5rem;border:none}.search-input .icon{position:absolute;left:0;top:50%;font-size:2.4rem;-webkit-transform:translate(50%, -50%);transform:translate(50%, -50%)}@media (max-width: 600px){.search-input{margin-bottom:3rem}}

.countries{display:flex;flex-wrap:wrap;max-width:100vw;justify-content:center}.filters{padding:3rem;display:flex;justify-content:space-between}@media (max-width: 600px){.filters{flex-direction:column}}

.btn{display:flex;align-items:center;padding:1rem 2rem;font-family:inherit;box-shadow:0px 0px 5px 1px rgba(0,0,0,0.4);border:none;background-color:transparent;border-radius:6px;cursor:pointer}.btn-icon{margin-right:1rem}

.country-page{display:flex;flex-direction:column;width:90%;margin:0 auto}.country-page .button{margin:5rem 0}.country-page .place{display:flex;flex-wrap:wrap}.country-page li{margin:1rem 2rem 1rem 0rem}.country-page .info{padding:4rem 1rem 4rem 0rem}.country-page .main-info{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between}@media (max-width: 1230px){.country-page .main-info{flex-direction:column}}.country-page .flag{display:flex;align-items:center}.country-page .flag img{max-width:60rem;width:100%;margin-right:4rem}@media (max-width: 400px){.country-page .flag img{max-width:90vw;width:100%}}.country-page span{font-weight:800}.country-page .border{padding:0.5rem 2rem;font-weight:600;box-shadow:0px 0px 2px 1px rgba(128,128,128,0.3);margin:0.5rem;cursor:pointer}.country-page .borders-info{display:flex;flex-wrap:wrap;align-items:center;margin:2rem 0}.country-page .borders-info .border{display:flex;align-items:center}.loading{min-height:calc(100vh - 75px);display:flex;justify-content:center;align-items:center}

*{padding:0;margin:0;box-sizing:border-box}html{font-size:62.5%}body{color:#111517;font-family:'Nunito Sans', sans-serif;background-color:#fafafa;margin:0 auto}body .icon,body select{color:#858585}body a{color:#111517}.app{transition:all .4s ease}.dark-theme{color:#fff;background-color:#202c37;transition:all .4s ease}.dark-theme input,.dark-theme select{transition:all .4s ease;background-color:#202c37}.dark-theme input::-webkit-input-placeholder, .dark-theme select::-webkit-input-placeholder{color:#fff}.dark-theme input:-ms-input-placeholder, .dark-theme select:-ms-input-placeholder{color:#fff}.dark-theme input::placeholder,.dark-theme input option,.dark-theme select::placeholder,.dark-theme select option{color:#fff}.dark-theme .icon,.dark-theme a{color:#fff}.homepage,.country-page{max-width:1440px;margin:0 auto;min-height:calc(100vh - 75px)}nav{background-color:#fff}h1{font-size:2.6rem}h2{font-size:2.2rem}h3{font-size:2rem}p,li,label,a,input,span{font-size:1.6rem}li{list-style-type:none}a{text-decoration:none}

