.btn{
    display: flex;
    align-items: center;
    padding: 1rem 2rem;
    font-family: inherit;
    box-shadow: 0px 0px 5px 1px rgba(black,.4);
    border: none;
    background-color: transparent;
    border-radius: 6px;
    cursor: pointer;
    &-icon{
        margin-right: 1rem;
    }
}